.DocumentPart8 {
  text-align: center;
  width: 100%;

  h1 {
    font-family: extra-bold;
    margin-top: 0;
    margin-bottom: 2px;
  }
}

.all {
  display: flex;
  width: 100%;
  height: 48vh;
  justify-content: center;
}

.list {
  width: 30%;
  padding: 0 20px;
  background-color: var(--backcolor);
  border-radius: 16px;
  font-family: bold;
}

.option {
  background-color: var(--backcolor);
  padding: 5px;
  border-radius: 8px;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
}

.right {
  width: 30%;

  p {
    margin: 0;
    opacity: 70%;
  }
}

.left {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown {
  width: 15%;
  background-color: white;
  padding: 5px;
  height: 50px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.center {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.detail {
  width: 100%;
  height: 200px;
  background-color: white;
  padding: 5px;
  border-radius: 4px;
  outline: 0;
  border: 0;
  resize: none;
  font-size: 16px;
  font-family: bold;
}

.footer {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.price {
  width: 100%;
  height: 50px;
  background-color: white;
  padding: 5px;
  border-radius: 4px;
  outline: 0;
  border: 0;
  resize: none;
  font-size: 16px;
  font-family: bold;
  display: flex;
  align-items: center;
}

.nextButton {
  min-width: 120px;
  display: flex;
  justify-content: space-around;
  padding: 10px;
  background-color: var(--importantcolor);
  border: 0;
  color: white;
  align-items: center;
  font-family: bold;
  font-size: .8em;
  border-radius: 8px;
  width: fit-content;
  margin: 15px auto;

  img {
    width: 1.5vw;
  }

  p {
    margin: 0;
  }
}

.displayBox {
  flex: 1;
  height: 50px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: right;
  padding: 0 10px;
  font-size: 16px;
  cursor: pointer;
  font-family: bold;
}

.arrowBox {
  width: 50px;
  height: 50px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.dropdownMenu {
  position: absolute;
  top: 110%;
  left: 0;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  z-index: 10;
  width: 100%;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

.dropdownItem {
  padding: 0.6rem 0.8rem;
  cursor: pointer;
}

.dropdownItem:hover {
  background-color: #f0f0f0;
}

.check {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  margin-top: 15px;
  justify-content: center;
  font-family: bold;
}

.checkbox {
  width: 30px;
  height: 30px;
  background-color: white;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.innerSquare {
  width: 15px;
  height: 15px;
  background-color: var(--importantcolor);
  border-radius: 4px;
  animation: inner linear 200ms;
}

.container {
  width: 100%;
  background-color: white;
  margin-top: 25px;
  border-radius: 16px;
  padding: 20px;
  font-family: bold;
}

.right {
  font-family: extra-bold;
  display: flex;
  align-items: center;

  p {
    margin: 0;
    margin-right: 15px;
  }

  h1 {
    margin-right: 15px;
  }
}

.filter {
  width: 100%;
  color: black;
  display: flex;
  justify-content: center;

  p {
    margin: 0;
    margin-right: 5px;
  }
}

.title {
  font-family: bold;
  padding: 5px 10px;
  background-color: var(--backcolor);
  margin-top: 10px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;

  .item {
    padding: 8px;
  }

  p {
    font-size: 12px;
  }

  .button {
    font-size: 12px;
    background-color: white;
    font-family: bold;
    padding: 8px;
  }

  .text {
    justify-content: center;
  }
}

.title {
  width: 100%;
  font-size: 16px;
  height: fit-content;
  background-image: linear-gradient(to left, var(--importantcolor) 1%, white);
  padding: 10px 5px;
}

.name {
  height: fit-content;
  width: 44px;

  p {
    margin: 0;
  }
}

.name:nth-child(1) {
  width: 50px;
}

.name:nth-child(2) {
  width: 15%;
}

.name:nth-child(3) {
  width: 40%;
}

.name:nth-child(4) {
  width: 25%;
}

.table {
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  max-height: 89%;
  padding-bottom: 50px;
  padding: 5px;
  border-radius: 8px;
  font-family: bold;
  position: relative;
}

.table::-webkit-scrollbar {
  width: 10%;
}

.row {
  padding: 5px;
  width: 100%;
  background-color: var(--backcolor);
  margin-top: 10px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;

  p {
    font-size: 1em;
  }
}

.item {
  padding: 7px;
  background-color: white;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: .9em;

  p {
    margin: 0;
  }
}

.history {
  border-radius: 8px;
  padding: 5px;
  width: 70%;
  height: 100%;
  background-color: white;
  position: relative;
}

.delete {
  color: white;
  width: 12%;
  padding: 5px;
  background-color: var(--importantcolor);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;

  p {
    margin: 0;
    margin-right: 5px;
  }
}

.edit {
  color: black;
  width: 8%;
  padding: 5px;
  background-color: #FFCFD0;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;

  p {
    margin: 0;
    margin-right: 5px;
  }
}

.item input {
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-family: bold;
  padding: 12px;
  outline: none;
  border-radius: 8px;
  border: 0;
}

.item:nth-child(1) {
  width: 50px;
}

.item:nth-child(2) {
  width: 15%;
}

.item:nth-child(3) {
  width: 40%;
}

.text {
  position: relative;
  width: 100%;
  cursor: default;
  display: flex;
}

.textShort {
  max-width: 300px;
  /* یا هر عرضی که مناسب جدولت باشه */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: rtl;
  /* چون متنت فارسیه */
}

.item:nth-child(4) {
  width: 25%;
}

.buttoncontainer {
  width: 20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  padding: 7px;
  font-size: 20px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--importantcolor);
  border: 0;
  color: white;
  font-family: bold;
}

.button img {
  width: 30px;
  height: 30px;
  object-fit: contain;
}



